@charset "UTF-8";
.fpt-sub-banner {
  position: relative;
}
.fpt-sub-banner .slogan {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}
@media (max-width: 576px) {
  .fpt-sub-banner .slogan {
    max-width: 12rem;
  }
  .fpt-sub-banner .slogan img {
    max-height: 1rem;
  }
}
.fpt-sub-banner .breadcrumb-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.fpt-sub-banner .breadcrumb-wrapper .container {
  background: rgba(242, 111, 33, 0.5);
}
.fpt-sub-banner .breadcrumb-wrapper ol {
  padding: 8px 0 8px 8px;
  list-style: none;
  display: flex;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li {
  margin-left: 16px;
  padding-left: 16px;
  position: relative;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li:before {
  position: absolute;
  left: -8px;
  top: 55%;
  transform: translateY(-50%);
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f142";
  color: #fff;
  font-size: 18px;
}
.fpt-sub-banner .breadcrumb-wrapper ol li a {
  color: #fff;
  text-decoration: none;
  white-space: nowrap;
  font-family: "Muli";
  font-size: 15px;
}
@media (max-width: 768px) {
  .fpt-sub-banner .breadcrumb-wrapper {
    display: none;
  }
}

.hoinhapquocte .container {
  padding-top: 50px;
  background: #fff;
  padding-bottom: 10px;
}
@media (max-width: 576px) {
  .hoinhapquocte .container {
    padding-bottom: 20px;
  }
}
.hoinhapquocte .bg {
  background: #FEEFE8;
}
.hoinhapquocte .pb-0 {
  padding-bottom: 0;
}
.hoinhapquocte .pt-30 {
  padding-top: 30px;
}
.hoinhapquocte .title {
  font-size: 35px;
  line-height: 35px;
  font-weight: 700;
  color: #333;
  text-align: center;
  text-transform: uppercase;
}
@media (max-width: 576px) {
  .hoinhapquocte .title {
    font-size: 24px;
    line-height: 24px;
    margin-bottom: 16px;
  }
}
.hoinhapquocte .btn-more {
  color: #f26f21;
  border-color: #f26f21;
}
.hoinhapquocte .btn-more:hover {
  background: #f26f21;
  color: #fff;
}
.hoinhapquocte .btn-more span:after {
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f35d";
  margin-left: 4px;
}

.tintuc-ct-1 .container {
  padding-top: 50px;
  background: #fff;
}
@media (max-width: 992px) {
  .tintuc-ct-1 a {
    text-decoration: none;
  }
}
.tintuc-ct-1 .main-news {
  font-family: "Muli" !important;
}
.tintuc-ct-1 .main-news .title {
  text-align: left;
  margin-bottom: 16px;
}
.tintuc-ct-1 .main-news .title h1 {
  font-size: 24px;
  line-height: 26px;
  color: #333 !important;
  font-weight: 700;
  text-transform: initial;
}
.tintuc-ct-1 .main-news .title time {
  font-family: Muli, sans-serif;
  font-size: 12px;
  font-weight: 600;
  color: #999;
}
.tintuc-ct-1 .main-news figure {
  margin-top: 16px;
}
.tintuc-ct-1 .main-news figure img {
  display: block;
  margin: 0 auto;
}
.tintuc-ct-1 .main-news figure figcaption {
  font-family: Muli, sans-serif;
  color: #666;
  font-size: 14px;
  width: 70%;
  text-align: center;
  margin: 5px auto 0;
}
@media (max-width: 992px) {
  .tintuc-ct-1 .main-news figure figcaption {
    width: 90%;
  }
}
.tintuc-ct-1 .main-news p {
  font-size: 16px;
  line-height: 1.5;
  font-family: "Muli";
}
.tintuc-ct-1 .title-lienquan {
  color: #333;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  margin: 16px 0;
  text-transform: uppercase;
}
.tintuc-ct-1 .tintuc-khac .title {
  font-size: 24px;
  font-weight: 700;
  padding: 9px 15px;
  background: #f26f21;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 16px;
}
.tintuc-ct-1 .tintuc-khac .thumbs-item {
  padding-bottom: 20px;
  box-shadow: 0 1px 0 0 #ddd;
}
.tintuc-ct-1 .tintuc-khac .thumbs-item:not(:first-child) {
  margin-top: 20px;
}
.tintuc-ct-1 .tintuc-khac .thumbs-item figure {
  display: flex;
}
.tintuc-ct-1 .tintuc-khac .thumbs-item figure img {
  transition: all 0.5s ease-in-out;
}
.tintuc-ct-1 .tintuc-khac .thumbs-item figure:hover img {
  transform: scale(1.1, 1.1);
}
.tintuc-ct-1 .tintuc-khac .thumbs-item figure .box-img {
  max-width: 40%;
  flex: 0 0 40%;
  height: 80px;
  overflow: hidden;
}
.tintuc-ct-1 .tintuc-khac .thumbs-item figure .box-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 992px) {
  .tintuc-ct-1 .tintuc-khac .thumbs-item figure .box-img {
    height: 150px;
  }
}
@media (max-width: 576px) {
  .tintuc-ct-1 .tintuc-khac .thumbs-item figure .box-img {
    height: 100px;
  }
}
.tintuc-ct-1 .tintuc-khac .thumbs-item figure figcaption {
  padding-left: 15px;
}
.tintuc-ct-1 .tintuc-khac .thumbs-item figure h5, .tintuc-ct-1 .tintuc-khac .thumbs-item figure time {
  font-weight: 600;
  font-family: Muli, sans-serif;
}
.tintuc-ct-1 .tintuc-khac .thumbs-item figure h5 {
  font-size: 13px;
  overflow: hidden;
  display: -webkit-box;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.tintuc-ct-1 .tintuc-khac .thumbs-item figure time {
  font-size: 12px;
  color: #999;
}
@media (max-width: 576px) {
  .tintuc-ct-1 .main-news .title h1 {
    font-size: 16px;
    line-height: 20px;
  }
  .tintuc-ct-1 .main-news .title time {
    font-size: 10px;
  }
  .tintuc-ct-1 .main-news figure figcaption {
    width: 100%;
    font-size: 10px;
  }
  .tintuc-ct-1 .main-news p {
    font-size: 13px;
    margin-top: 10px;
  }
  .tintuc-ct-1 .title-lienquan {
    font-size: 16px;
    margin-bottom: 10px;
  }
  .tintuc-ct-1 .tintuc-khac .title {
    font-size: 16px;
    line-height: 16px;
  }
}